header {
  width: 100vw;
  height: 60px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1000;
}

.nav-container {
  margin-right: 2%;
  width: 45%;
  height: 100%;
  /*background-color: blue;*/
  display: flex;
  align-items: center;
  justify-content: space-around;
}

.nav-section-basket-components {
  height: 75%;
  display: flex;
  flex-direction: row;
}

.nav-section-basket-total {
  font-size: 1.5em;
  font-weight: 500;
  display: flex;
  align-items: center;
}

/* Light and Dark Theme classes*/
.header-light-theme {
  background-color: var(--color-light);
  border-bottom: 0.1rem solid black;
}

.header-dark-theme {
  background-color: var(--color-dark);
  border-bottom: 0.1rem solid white;
}

/* Responsive Web Design */
@media screen and (max-width: 800px){
  .nav-section-basket-total {
    display: none;
  }
}

@media screen and (max-width: 1000px) {
  .nav-container {
    width: 60%;
  }
}
